.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: 28px;

  line-height: normal;
  text-align: center;
  font-size: 0;
margin-top: 40px;

@include mq(tablet) {
  margin-top: 60px;
}
 
}
.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 24px;
  padding: 8px 0 6px;
 
  color: #333;
  font-size: 11px;
  font-weight: normal;
  line-height: 1;
  text-decoration: none;
  vertical-align: middle;
  border: 1px solid #ddd;
  border-width: 1px 0;
  border-radius:5px;

   @include mq(tablet) {
       width: 35px;
    height: 30px;
    font-size: 16px;
   }

    @include mq(desktop) {
    width: 47px;
    height: 40px;
    font-size: 21px;
    }
}

.tui-page-btn:not(:first-child) {
  margin-left:5px;
}


.tui-pagination .tui-page-btn:hover {
  background-color: red;
}
.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: #e67e4b;
  border-color: #e67e4b;
  cursor: default;
}
.tui-pagination .tui-is-selected:hover {
  background-color: #e67e4b;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 28px;
  padding: 0;
  font-size: 0;
  line-height: 28px;
  white-space: nowrap;
  font-weight: normal;
  border: 1px solid #ddd;
  
   @include mq(tablet) {
    height:26px;
   }

   @include mq(desktop) {
    height:32px;  
   }
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}


.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;

}
.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}
.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}
.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}
.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-is-disabled {
  background-color:#F7F7F7;
  
}


.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
}
.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}
.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}
.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}
.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}
.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}
.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}
.tui-page-inputs .tui-page-current {
  color: #e67e4b;
}
.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

