  justify-content: center;
  align-items: center;
  padding: 30px 10px;
  display: flex;
}

.modalTeam__about {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding-bottom: 25px;
  display: flex;
}

.modalTeam {
  max-width: 340px;
  background-color: rgba(66, 66, 66, .9);
  border-radius: 25px;
  flex-direction: row;
  align-items: center;
  padding: 1rem;
  transition: all 1.2s;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .modalTeam {
    max-width: 700px;
  }
}

@media screen and (min-width: 1280px) {
  .modalTeam {
    max-width: 1000px;
  }
}

.modalTeam__title {
  letter-spacing: .03px;
  text-align: center;
  color: var(--button-text-color);
  cursor: pointer;
  justify-content: center;
  margin-top: 25px;
  margin-bottom: 25px;
  font-size: 45px;
  line-height: 1.17;
  position: relative;
}

.modalTeam__title:hover, .modalTeam__title:focus {
  color: var(--accent-color);
}

.modal__icon {
  text-transform: uppercase;
  cursor: pointer;
  color: #d0c9c9;
  background: none;
  border-radius: 12px;
  outline: none;
  padding: 4px 10px;
  font-size: 26px;
  transition: all .3s;
  position: absolute;
  top: 15px;
  right: 15px;
}

.modal__icon:hover, .modal__icon:focus {
  color: var(--accent-color);
}

.modalTeam__list {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.modalTeam__img {
  cursor: pointer;
  width: 60px;
  height: 60px;
  border-radius: 25%;
  margin: 10px;
  transition: all .3s ease-in-out;
  transform: scale(1);
}

@media screen and (min-width: 768px) {
  .modalTeam__img {
    width: 110px;
    height: 110px;
    margin: 10px;
  }
}

.modalTeam__img:hover {
  transform: scale(1.3);
}

.modalTeam__style.fix {
  background-color: rgba(93, 87, 83, .9);
  border-radius: 20%;
  margin: 10px;
  box-shadow: 0 0 22px #7d7a79;
}

.modalTeam__caption {
  text-align: center;
}

.modalTeam__name {
  cursor: pointer;
  margin-bottom: 10px;
  font-size: 16px;
}

.modalTeam__position {
  color: var(--accent-color);
  cursor: pointer;
  margin: 2px 0 9px;
  font-size: 14px;
}

.modalTeam__position:hover, .modalTeam__position:focus {
  color: var(--second-bgc);
}

.is-hidden-btn {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  display: none;
}

.waviy {
  -webkit-box-reflect: below -30px linear-gradient(transparent, rgba(0, 0, 0, .09));
  font-size: 0;
  position: relative;
}

.waviy span {
  font-size: 30px;
  font-family: var(--primary-font);
  color: #fff;
  animation: waviy 1s backwards;
  animation-delay: calc(.1s * var(--i) );
  transition: transform .2s ease-in-out;
  display: inline-block;
  position: relative;
}

.waviy span:hover, .waviy span:focus {
  color: var(--accent-color);
  transform: rotateZ(10deg)scale3d(1.1, 1.1, 1);
}

@keyframes waviy {
  0%, 40%, 50% {
    transform: translateY(0);
  }

  20% {
    transform: translateY(-25px);
  }
}

