* {
  border: 0;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --bg: hsl(223deg, 10%, 90%);
  --trans-dur: 0.3s;
  font-size: calc(16px + 4 * (100vw - 320px) / 960);
}

.bcdrop-spiner {
  background-color: rgba(0, 0, 0, 0.614);
  z-index: 99999;
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  visibility: hidden;

  &.visible {
    visibility: visible;
  }
}

.tvdd {
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 16em;
  height: 16em;

  transform: translate(-50%, -50%);
}
.tvdd__ring {
  top: 0;
  left: 18.75%;
  width: 62.5%;
  height: 62.5%;
  transform-origin: 50% 80%;
}
.tvdd__ring,
.tvdd__ring-dot {
  position: absolute;
}
.tvdd__ring-dots {
  animation: pivot 1s linear infinite;
  width: 100%;
  height: 100%;
}
.tvdd__ring-dot {
  animation: pulse 1s ease-in-out infinite;
  background-color: rgb(255, 136, 0);
  border-radius: 50%;
  margin: -5% 0 0 -5%;
  width: 10%;
  height: 10%;
}
.tvdd__ring-dot:nth-child(1) {
  top: 38.3531429704%;
  left: 6.533337817%;
}
.tvdd__ring-dot:nth-child(2) {
  animation-delay: -0.0833333333s;
  top: 61.6468570296%;
  left: 6.533337817%;
}
.tvdd__ring-dot:nth-child(3) {
  animation-delay: -0.1666666667s;
  top: 81.8198051534%;
  left: 18.1801948466%;
}
.tvdd__ring-dot:nth-child(4) {
  animation-delay: -0.25s;
  top: 93.466662183%;
  left: 38.3531429704%;
}
.tvdd__ring-dot:nth-child(5) {
  animation-delay: -0.3333333333s;
  top: 93.466662183%;
  left: 61.6468570296%;
}
.tvdd__ring-dot:nth-child(6) {
  animation-delay: -0.4166666667s;
  top: 81.8198051534%;
  left: 81.8198051534%;
}
.tvdd__ring-dot:nth-child(7) {
