.theme-switch {
    position: absolute;
    top: 155px;
    right: 3%;
    display: flex;
    align-items: center;
    @include mq(tablet) {
      top: 120%;
        right: 1%;
      }
    
      @include mq(desktop) {
        right: 1%;
      }
  }
  
  .theme-switch__icon {
    color: var(--accent-color);
    height: 16px;
    width: 16px;
    margin-right: 8px;
    margin-left: 8px;
  }
  
  .theme-switch__control {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    position: relative;
    width: 50px;
    height: 12px;
    border-radius: 50em;
    padding: 3px 0;
  }
  
  .theme-switch__toggle {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
    height: 0;
    width: 0;
    pointer-events: none;
  }
  
  .theme-switch__track {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: transparent;
    user-select: none;
    background-color: #333;
    border-radius: inherit;
    z-index: 1;
    cursor: pointer;
  }
  
  .theme-switch__marker {
   
    background-color: #fff;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    z-index: 2;
    pointer-events: none;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
    transform: translateX(3px);
   transition: var(--timing-function);
  }
  
  .theme-switch__toggle:checked + .theme-switch__track {
    background-color: #d3d3d4;
  }
  
  .theme-switch__toggle:checked ~ .theme-switch__marker {
    transform: translateX(35px);
  }
  
  .theme-switch__toggle:focus + .theme-switch__track,
  .theme-switch__toggle:active + .theme-switch__track {
    box-shadow: 0 0 0 3px var( --accent-color);
  }